<template>
    <div class="header">
        <div class="container">
            <div class="header-menu">
                <router-link to="/" class="logo"></router-link>
                <router-link to="/" class="menu active">
                    <b></b>
                    <span></span>
                </router-link>
                <router-link to="/about" class="menu">
                    <b> </b>
                    <span> </span>
                </router-link>
                <router-link to="/news" class="menu">
                    <b></b>
                    <span>  </span>
                </router-link>
                <router-link to="/faq" class="menu">
                    <b>FAQ</b>
                    <span>/</span>
                </router-link>
                <router-link to="/reviews" class="menu">
                    <b></b>
                    <span>   ?</span>
                </router-link>
                <router-link to="/contacts" class="menu">
                    <b></b>
                    <span>  </span>
                </router-link>
                <template v-if="String(this.$router.currentRoute.value.path).search('dashboard') !== -1">
                    <div class="account-balance ml-auto" v-if="balance.USDT">
                        <div class="account-balance-wrap">
                            <span> </span>
                            <b>{{ balance.USDT.balance }}</b>
                        </div>
                    </div>
                </template>
                <template v-else>
                    <a href="#" class="auth-menu ml-auto" v-if="isAuth" v-on:click="logout"></a>
                    <router-link to="?page=login" class="auth-menu ml-auto" v-else></router-link>
                    <router-link to="/dashboard" class="auth-menu active" v-if="isAuth"> </router-link>
                    <router-link to="?page=registration" class="auth-menu active" v-else></router-link>
                </template>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            isAuth: false
        }
    },
    created() {
        if (localStorage.getItem('isAuth') === 'true' && localStorage.getItem('token')) {
            this.isAuth = true
        }
    },
    computed: {
        balance() {
            return (this.$store.getters.getCurrencies)
            ? this.$store.getters.getCurrencies
            : this.$store.dispatch('getCurrencies')
        }
    },
    methods: {
        logout() {
            this.$store.dispatch('logout')
            this.isAuth = false
        }
    }
}
</script>